.description__container {
/*  width: 290px;
  margin: 0 auto;*/
}

.description__title {

  &--bg {
    background: url("../img/dicscover.png") no-repeat;
    background-position: 3px 30px;
  }


  &::before {
    content: "что  вас  ждёт";
    top: 38px;
    left: -70px;
    font-size: 100px;
    font-family: "Besom 2";
    font-weight: 100;
    color: rgba(55, 59, 65, 0.102);
    text-transform: uppercase;
    line-height: 0.2;
    text-align: center;
    position: absolute;
    width: 100%;
  }
}

.description__list {
  list-style: none;
  padding: 0;
  color: #4c4c4c;
  text-align: center;
}

.description__span {
  color: #ff0000;
  font-size: 24px;
  line-height: 24px;
  position: relative;
  margin-left: 8px;
  display: inline-block;

  &--start {
    margin-bottom: 33px;
  }

    &::before {
      content: "";
      position: absolute;
      background: #ffe5e5;
      width: 20px;
      height: 20px;
      border-radius: 50%;
      top: 42px;
      left: 19px;
    }

    &::after {
      content: "";
      position: absolute;
      background: #ff0000;
      width: 12px;
      height: 12px;
      border-radius: 50%;
      top: 46px;
      left: 22px;
    }

    &--finish {

      margin: 67px 0 0 3px;

        &::before {
          top: -34px;
          left: 22px;;
        }

        &::after {
          top: -30px;
          left: 25px;
        }
      }
}

.description__sub-title {
  font-size: 20px;
  font-weight: 700;
  margin: 18px 0 0 0;
}

.description__text {
  font-size: 16px;
  line-height: 24px;
  margin: 11px 11px 15px 16px;
}

.description__wrapper-image {
  margin-top: 40px;

  &--margin {
    margin-bottom: 10px;
  }
}

.description__items {
  font-family: "besom 2", sans-serif;
  font-size: 122.5px;
  line-height:20px;
  opacity: 0.1;
  background: url("");
}

@media (min-width: 750px) {
  .description__item {
    display: flex;
    position: relative;
    align-items: center;
    text-align: left;

    &:nth-child(1) {
      .description__wrapper-image {
        margin: 35px 28px 0 18px;
      }
    }

    &:nth-child(2) {
      flex-direction: row-reverse;

      .description__wrapper-image {
        margin: 21px -2px 0 18px
      }

      .description__wrapper-exposition {
        margin: 16px 15px 0 146px;
      }
    }

    &:nth-child(3) {
      .description__wrapper-image {
        margin: -9px 0 0 5px
      }

      .description__wrapper-exposition {
        margin: -41px 80px 0 16px;
      }
    }

    &:nth-child(4) {
      flex-direction: column;
      align-items: flex-end;

      .description__wrapper-exposition {
        margin: 12px 0 0 459px;
      }

      .description__wrapper-image {
        margin: -55px 30px 0 0;
      }
    }
  }

  .description__wrapper-exposition {
    margin: 28px 84px 0 0;
  }

  .description__list {
    margin-top: 89px;
    position: relative;

    &::before {
      content: "";
      position: absolute;
      top: -25px;
      right: 107px;
      width: 506px;
      height: 1050px;
      background: url("../img/line.svg") no-repeat;
    }
  }

  .description__title {
    font-size: 48px;
    margin: 11px 0 20px 11px;
    padding: 30px 0;
/*    background: url("../img/bg-title-table.png") no-repeat;*/
    background-position: 16px -12px;
  }

  .description__span {
    position: absolute;

    &--start {
      top: 14px;
      right: 94px;
    }

    &--finish {
      top: 81px;
      left: 214px;
    }
  }
  .description__text {
    margin-left: 0;
  }

}


@media (min-width: 1170px) {
  .description__title {
    font-size: 68px;
    margin: 114px 15px 0 0;

    &::before {
      font-size: 180px;
      top: 54px;
      left: 10px;
    }
  }

  .description__item {
    &:nth-child(2) {
      flex-direction: row;
      justify-content: flex-end;
      .description__wrapper-image {
        margin: 21px -2px 0 18px
      }
    }

    &:nth-child(4) {
      flex-direction: row;
      justify-content: flex-end;

      .description__wrapper-exposition {
        margin: 12px 0 0 0;
      }
    }
  }

  .description__wrapper-exposition {
    width: 300px;
    margin: 0;
  }

  .description__span {

    &--start {
      top: 32px;
      right: 284px;
    }

    &--finish {
      top: 104px;
      left: 219px;
    }
  }

  .description__list {

    &::before {
      top: -8px;
      left: 139px;
      width: 700px;
      height: 1050px;
      background: url("../img/line-desktop.svg") no-repeat;
    }
  }

  .description__wrapper-image {
    transform: scale(1.2);
  }

  .description__item {
    &:nth-child(1) {
      .description__wrapper-image {
        margin: 48px 64px 0 46px;
      }

      .description__wrapper-exposition {
        margin-top: 81px;
      }
    }

    &:nth-child(2) {
      .description__wrapper-image {
        margin: 29px -85px 0 8px;
        transform: scale(1.3);
      }

      .description__wrapper-exposition {
        margin: 28px 0 0 145px;
      }
    }

    &:nth-child(3) {
      .description__wrapper-image {
        margin: -11px 27px 0 -10px;
        transform: scale(1.1);
      }

      .description__wrapper-exposition {
        margin-top: -25px;
      }
    }

    &:nth-child(4) {
      .description__wrapper-image {
        margin: -25px 60px 0 0;
      }

     .description__wrapper-exposition {
        align-items: flex-start;
        padding-left: 13px;
        box-sizing: border-box;
      }
    }
  }
}

